<template>
  <div>
    <tab-page ref="tabPage">
      <template slot="h5Statistics">
        <data-table @exportDataSuccess="exportDataSuccess" :data-table="dataTable" ref="dataTable">
          <div slot="conditions" style="margin-bottom:15px">
            <el-form :inline="true" :model="queryParams">
              <el-form-item>
                <el-date-picker
                  v-model="queryParams.beginTime"
                  type="date"
                  placeholder="选择日期时间"
                  size="small"
                  style="width:170px"
                ></el-date-picker>
              </el-form-item>
              <span class="span-style">至</span>
              <el-form-item>
                <el-date-picker
                  v-model="queryParams.endTime"
                  type="date"
                  placeholder="选择日期时间"
                  size="small"
                  style="width:170px"
                ></el-date-picker>
              </el-form-item>
              <el-form-item>
                <el-select
                  v-model="queryParams.intervalMinutes"
                  placeholder="分组时间间隔"
                  size="small"
                  clearable
                >
                  <el-option key="10" label="10(min)" value="10"></el-option>
                  <el-option key="20" label="20(min)" value="20"></el-option>
                  <el-option key="30" label="30(min)" value="30"></el-option>
                  <el-option key="40" label="40(min)" value="40"></el-option>
                  <el-option key="50" label="50(min)" value="50"></el-option>
                  <el-option key="60" label="60(min)" value="60"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-select
                  v-model="queryParams.device"
                  clearable
                  filterable
                  placeholder="设备"
                  size="small"
                >
                  <el-option
                    v-for="item in deviceOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item>
                <el-select
                  v-model="queryParams.source"
                  clearable
                  placeholder="source"
                  multiple
                  collapse-tags
                  size="small"
                >
                  <el-option
                    v-for="item in sourceOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-select
                  v-model="queryParams.isksjk"
                  clearable
                  filterable
                  placeholder="快速借款"
                  size="small"
                >
                  <el-option
                    v-for="item in ksjkOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit" size="small" style="margin-top:0">查询</el-button>
              </el-form-item>
            </el-form>
            <el-button
              class="right mb-10"
              type="primary"
              size="small"
              icon="el-icon-document"
              @click="exportExcel"
            >导出excel</el-button>
            <div
              style="width:90%;margin-top:10px;"
              :class="{hidden:!showProgress, show:showProgress}"
            >
              <el-progress
                class="process-item"
                :color="customerColor"
                :stroke-width="10"
                :percentage="downloadPercentage"
                :status="downloadStatus"
              ></el-progress>
            </div>
          </div>
          <template slot="columns">
            <el-table-column type="index" label="序号" width="60"></el-table-column>
            <el-table-column sortable prop="date" label="日期" width="120"></el-table-column>
            <el-table-column sortable prop="plat" label="设备" width="100"></el-table-column>
            <el-table-column sortable prop="ksjk_existing" label="快速借款用户" width="90"></el-table-column>
            <el-table-column sortable prop="source" label="source" width="150"></el-table-column>
            <el-table-column sortable prop="activation_count" label="激活"></el-table-column>
            <el-table-column sortable prop="register_count" label="注册"></el-table-column>
            <el-table-column sortable prop="jinjian" label="当日进件"></el-table-column>
            <el-table-column sortable prop="reject" label="当日拒绝"></el-table-column>
            <el-table-column sortable prop="approval" label="当日审核"></el-table-column>
            <el-table-column sortable prop="approval_low" label="当日审核(额度<=1000)" width="125"></el-table-column>
            <el-table-column sortable prop="approval_high" label="当日审核(额度>1000)" width="120"></el-table-column>
            <el-table-column sortable prop="emitCn" label="当日放款"></el-table-column>
            <el-table-column sortable prop="emitMoney" label="放款金额"></el-table-column>
            <el-table-column sortable prop="act_reg_rate" label="注册/激活" width="90"></el-table-column>
            <el-table-column sortable prop="jj_reg_rate" label="进件/注册" width="90"></el-table-column>
            <el-table-column sortable prop="review_jinj_rate" label="审核/进件" width="90"></el-table-column>
            <el-table-column
              sortable
              prop="review_low_jinj_rate"
              label="审核/进件(额度<=1000)"
              width="130"
            ></el-table-column>
            <el-table-column
              sortable
              prop="review_high_jinj_rate"
              label="审核/进件(额度>1000)"
              width="120"
            ></el-table-column>
            <el-table-column sortable prop="jinjian_t7" label="T7进件"></el-table-column>
            <el-table-column sortable prop="approval_t7" label="T7审核"></el-table-column>
            <el-table-column sortable prop="approval_t7_low" label="T7审核(额度<=1000)" width="120"></el-table-column>
            <el-table-column sortable prop="approval_t7_high" label="T7审核(额度>1000)" width="120"></el-table-column>
            <el-table-column sortable prop="emitCn_t7" label="T7放款"></el-table-column>
            <el-table-column sortable prop="emitMoney_t7" label="T7放款金额"></el-table-column>
          </template>
        </data-table>
      </template>
    </tab-page>
  </div>
</template>
<style scoped>
.el-form-item__content .el-button {
  margin-top: 20px;
}

.el-table--border th {
  text-align: center;
}

.el-table th .cell {
  text-align: center;
}

.el-form--inline .el-form-item {
  margin-right: 2px;
}

.el-form-item {
  margin-bottom: 2px;
}

.el-dialog__footer {
  padding: 10px 0px;
  text-align: center;
}

.check-all-select {
  padding: 10px;
}

.check-all-select span.text-all {
  font-size: 14px;
  font-weight: bold;
}

.check-all-select label {
  float: right;
}

.mb-10 {
  margin-bottom: 10px;
}
.show {
  display: block;
}
.hidden {
  display: none;
}
</style>
<script>
import common from '../../../utils/common';
import formatDate from '../../../utils/formatDate';
import DataTable from '../../../components/DataTable';
import TabPage from '../../../components/TabPage';
import { Notification } from 'element-ui';
export default {
  components: {
    DataTable,
    TabPage
  },
  data() {
    return {
      sourceCheckAll: false,

      deviceCheckAll: false,

      sourceOptions: [],
      deviceOptions: [
        {
          value: 'iOS',
          label: 'iOS'
        },
        {
          value: 'Android',
          label: 'Android'
        }
      ],
      ksjkOptions: [
        {
          value: '是',
          label: '是'
        },
        {
          value: '否',
          label: '否'
        }
      ],
      initBeginTime: '',
      initEndTime: '',

      queryParams: {
        intervalMinutes: '',
        source: '',
        device: '',
        isksjk: '',
        beginTime: [], // 开始时间
        endTime: [] // 结束时间
      },
      dataTable: {
        pageSize: 50,
        pageSizes: [10, 20, 50, 100, 1000, 5000, 10000, 20000],
        url: '/web/v4/market/queryMarketDetail', // 代付管理
        emptyText: '没有记录',
        showSummary: true,
        summaryMethod: this.getSummaries
      },
      downloadPercentage: 0,
      downloadStatus: "warning",
      showProgress: false,
      customerColor: '#409eff'
    };
  },
  mounted() {
    this.dataTable.queryParams = this.queryParams;
    let date = new Date();
    let beginDate = new Date(date.setDate(date.getDate() - 7));
    this.initBeginTime = this.dealDate(beginDate);
    this.initEndTime = this.dealDate(new Date());
    this.dataTable.queryParams.beginTime = this.initBeginTime + ' 00:00:00';
    this.dataTable.queryParams.endTime = this.initEndTime + ' 23:59:59';

    var defaultPage = {
      label: '市场厂商类',
      name: 'h5Statistics',
      slotName: 'h5Statistics',
      closable: false
    };
    this.$refs.tabPage.$emit('open-tab', defaultPage);

    this.getSources();
  },
  methods: {
    loadLists() {
      this.$refs.dataTable.$emit('load-data');
    },

    onSubmit: function() {
      this.dataTable.queryParams = this.queryParams;
      var tag = this.warnTime(
        this.dataTable.queryParams.beginTime,
        this.dataTable.queryParams.endTime,
      );
      if (tag) {
        return;
      }
      // console.log(Object.prototype.toString.call(this.dataTable.queryParams.beginTime));
      this.dataTable.queryParams.beginTime =
        this.dealDate(this.dataTable.queryParams.beginTime) + ' 00:00:00';
      this.dataTable.queryParams.endTime =
        this.dealDate(this.dataTable.queryParams.endTime) + ' 23:59:59';

      this.dataTable.url = '/web/v4/market/queryMarketDetail';
      this.$refs.dataTable.$emit('load-data');
    },
    warnTime(startTime, endTime) {
      if (!startTime) {
        this.$notify({
          message: '查询开始时间不能为空！',
          type: 'warning'
        });
        return true;
      }
      if (!endTime) {
        this.$notify({
          message: '查询结束时间不能为空！',
          type: 'warning'
        });
        return true;
      }
      let timeStart = new Date(startTime).getTime();
      let timeEnd = new Date(endTime).getTime();
      if (timeStart > timeEnd) {
        this.$notify({
          message: '查询开始时间不能大于结束时间',
          type: 'warning'
        });
        return true;
      }
      return false;
    },
    dealTime(time) {
      return formatDate(new Date(time), 'yyyy-MM-dd hh:mm:ss');
    },
    formatterDate(row, column) {
      return formatDate(row.date, 'yyyy-MM-dd');
    },
    dealDate(date) {
      return formatDate(new Date(date), 'yyyy-MM-dd');
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '总计';
          return;
        }
        var values;
        var itemProperty = column.property;
        if (
          itemProperty === 'act_reg_rate' ||
          itemProperty === 'jj_reg_rate' ||
          itemProperty === 'review_jinj_rate' ||
          itemProperty === 'review_low_jinj_rate' ||
          itemProperty === 'review_high_jinj_rate'
        ) {
          values = data.map(item => {
            if (item[column.property] != null) {
              return Number(item[column.property].replace('%', ''));
            }
          });
        } else {
          values = data.map(item => Number(item[column.property]));
        }

        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (
            itemProperty === 'act_reg_rate' ||
            itemProperty === 'jj_reg_rate' ||
            itemProperty === 'review_jinj_rate' ||
            itemProperty === 'review_low_jinj_rate' ||
            itemProperty === 'review_high_jinj_rate'
          ) {
            sums[index] = (sums[index] / data.length).toFixed(2) + ' %';
          }
          // sums[index] += ' 元';
        } else {
          sums[index] = 'N/A';
        }
      });

      return sums;
    },

    exportExcel() {
      this.dataTable.queryParams = this.queryParams;
      var tag = this.warnTime(
        this.dataTable.queryParams.beginTime,
        this.dataTable.queryParams.endTime,
      );
      if (tag) {
        return;
      }
      // console.log(Object.prototype.toString.call(this.dataTable.queryParams.beginTime));
      this.dataTable.queryParams.beginTime =
        this.dealDate(this.dataTable.queryParams.beginTime) + ' 00:00:00';
      this.dataTable.queryParams.endTime =
        this.dealDate(this.dataTable.queryParams.endTime) + ' 23:59:59';
      var exportParams = {};
      Object.assign(exportParams, this.dataTable.queryParams);
      exportParams.pageSize = this.$refs.dataTable.total;
      exportParams.page = 1;
      this.$refs.dataTable.$emit('export-data', exportParams);
    },
    exportDataStatus(statusInfo) {
      if (statusInfo.status == 2) {
        this.downloadPercentage = 100;
        this.downloadStatus = 'success';
      } else if (statusInfo.status == -1) {
        this.downloadStatus = 'exception';
      } else {
        this.downloadPercentage = statusInfo.percent;
        console.log(statusInfo.percent);
        console.log(this.downloadPercentage);
        this.showProgress = true;
      }
    },
    exportDataSuccess() {
      console.log('success');
      require.ensure([], () => {
        const { export_json_to_excel } = require('vendor/Export2Excel');
        const tHeader = [
          '日期',
          '设备',
          '快速借款用户',
          'source',
          '激活',
          '注册',
          // '拒绝',
          '当日进件',
          '当日审核',
          '当日审核(额度<=1000)',
          '当日审核(额度>1000)',
          '当日放款',
          '放款金额',
          '注册/激活',
          '进件/注册',
          '审核/进件',
          '审核/进件(额度<=1000)',
          '审核/进件(额度>1000)',
          'T7进件',
          'T7审核',
          'T7审核(额度<=1000)',
          'T7审核(额度>1000)',
          'T7放款',
          'T7放款金额'
        ];
        const filterVal = [
          'date',
          'plat',
          'ksjk_existing',
          'source',
          'activation_count',
          'register_count',
          'jinjian',
          // 'reject',
          'approval',
          'approval_low',
          'approval_high',
          'emitCn',
          'emitMoney',
          'act_reg_rate',
          'jj_reg_rate',
          'review_jinj_rate',
          'review_low_jinj_rate',
          'review_high_jinj_rate',
          'jinjian_t7',
          'approval_t7',
          'approval_t7_low',
          'approval_t7_high',
          'emitCn_t7',
          'emitMoney_t7'
        ];
        setTimeout(() => {
          const list = this.$refs.dataTable.dataListA;
          const data = this.formatJson(filterVal, list);
          export_json_to_excel(tHeader, data, '市场厂商类');
        }, 500);
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v =>
        filterVal.map(j => {
          let returnValue;
          if (j == 'operator') {
            returnValue = v[j].name;
          } else {
            returnValue = v[j];
          }
          return returnValue;
        }),
      );
    },

    getSources() {
      common.ajax({
        url: '/web/v2/options/market/sources',
        type: 'POST',
        contentType: 'application/json',
        success: response => {
          if (response.data && response.data.list) {
            this.sourceOptions = response.data.list;
          }
        },
        error: err => {
          let error = err.message || err;
          Notification.error({
            title: '错误',
            message: error
          });
        }
      });
    },

    checkAllSelection(selectType) {
      let len = this.$refs[selectType].optionsCount;
      // let seletedLen =  this.$refs[selectType].selected.length;

      for (var i = 0; i < len; i++) {
        (function(index, _this) {
          setTimeout(() => {
            _this.$refs[selectType].handleOptionSelect(
              _this.$refs[selectType].options[index],
            );
          }, 0);
        })(i, this);
      }
    },
    handleCheckAllDeviceChange(event) {
      this.checkAllSelection('deviceSelect');
      this.deviceCheckAll = !this.deviceCheckAll;
    },
    handleCheckAllSourceChange(event) {
      this.checkAllSelection('sourceSelect');
      this.sourceCheckAll = !this.sourceCheckAll;
    },
    setCheckAllStyle(selectedCount, selectType) {
      let type = selectType + 'CheckAll';
      let type_1 = selectType + 'Select';
      if (this[type]) {
        if (selectedCount.length < this.$refs[type_1].optionsCount) {
          this[type] = !this[type];
        }
      } else {
        if (selectedCount.length === this.$refs[type_1].optionsCount) {
          this[type] = !this[type];
        }
      }
    },
    deviceChange(values) {
      this.setCheckAllStyle(values, 'device');
    },
    sourceChange(values) {
      this.setCheckAllStyle(values, 'source');
    }
  }
};
</script>
<style>
.span-style {
  display: inline-block;
  margin-top: 8px;
  font-size: 14px;
}
</style>
